



function Products(){
    return (
        <div>
            <form action="/api/product" method="POST" encType="multipart/form-data">
                Product name: <input type="text" name="name"></input>
                Product image: <input type="file" name="product_image"></input>
                <input type="hidden" value="Kevin" name="user"></input>
                <button type="submit">Send</button>
            </form>
        </div>
    )
}

export default Products